import { Message } from 'element-ui';

export default {
  name: 'Content_menu3',
  props: {
      activeMenu: {
          type: String,
          required: true, 
      },
  },
  data() {
      return {
        pdfUrl: '', // PDF 文件的 URL
        filterForm: {
            date: '2024-10-18',//this.getDefaultDate(), // 日期筛选值，设置初始值为当前日期
        },
        pdfExists: false, // 初始状态为文件不存在
      };
  },
  mounted() {
    this.checkPdfExists();
  },
  methods: {
      fetchPdf() {
        this.pdfUrl = `http://localhost:3000/menu/${this.activeMenu}/date/${this.filterForm.date}/pdf`;
        this.checkPdfExists();
      },
      async checkPdfExists() {
        try {
          const response = await fetch(this.pdfUrl, { method: 'HEAD' });
          if (response.ok) {
            this.pdfExists = true;
          } else {
            this.pdfExists = false;
            Message.warning('该日期暂无数据');
          }
        } catch (error) {
          this.pdfExists = false;
          Message.warning('该日期暂无数据');
        }
      },
  },
  created() {
    // 页面加载时
    this.fetchPdf();
  },
}