
import * as echarts from 'echarts';
import { Message } from 'element-ui';
import axios from 'axios';

export default {
  name: 'Content_menu1_4',
  props: {
    activeMenu: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      filterForm: {
        date: '2024-10-18', // 初始日期
      },
      tableData: [], // 表格数据
      ai_word: '', // AI 输出内容
      isDataLoaded: false, // 数据加载状态
      lineChart: null, // ECharts 实例
    };
  },
  created() {
    this.fetchData();
  },
  computed: {
        // ai分析框内的文本换行符置换
        formattedAiWord() {
            // 将 \n 替换为 <br>
            return this.ai_word.replace(/\n/g, '<br>');
            },
  },
  methods: {
    // 获取数据
    async fetchData() {
      try {
        const response = await axios.get(
          `http://localhost:3000/menu/${this.activeMenu}/date/${this.filterForm.date}/csv`
        );
        console.log('获取到的数据1:', response.data);
        const response_aiword = await axios.get(
          `http://localhost:3000/menu/${this.activeMenu}/date/${this.filterForm.date}/txt`
        );
        console.log('获取到的数据2:', response_aiword.data);
        this.ai_word = response_aiword.data;
        this.tableData = response.data;
        this.isDataLoaded = true;
        this.renderLineChart(); // 数据加载完成后渲染图表
      } catch (error) {
        console.log('该日无数据');
        this.ai_word = '无';
        this.tableData = [];
        this.isDataLoaded = true;
        Message.warning('该日期暂无数据');
        this.clearChart(); // 清空图表
      }
    },
    // 渲染折线图
    renderLineChart() {
      if (this.lineChart) {
        this.lineChart.dispose(); // 销毁之前的图表实例
      }
      this.lineChart = echarts.init(this.$refs.lineChart);

      const xAxisData = this.tableData.map((row) => row['时间']); // 时间作为 X 轴
      const seriesDataAll = this.tableData.map((row) => row['全部(亿股)']); // 全部数据
      const seriesDataBuy = this.tableData.map((row) => row['申购(亿股)']); // 申购数据
      const seriesDataSell = this.tableData.map((row) => row['赎回(亿股)']); // 赎回数据

      const option = {
        tooltip: {
          trigger: 'axis',
        },
        legend: {
          data: ['全部', '申购', '赎回'],
          bottom: 10,
        },
        xAxis: {
          type: 'category',
          data: xAxisData,
          axisLabel: {
            rotate: 45, // X 轴标签旋转 45 度
          },
        },
        yAxis: {
          type: 'value',
          name: '亿股',
        },
        series: [
          {
            name: '全部',
            type: 'line',
            data: seriesDataAll,
            smooth: true, // 平滑曲线
            lineStyle: {
              color: '#5470C6', // 蓝色
            },
            itemStyle: {
                color: '#5470C6', // 蓝色
            },
          },
          {
            name: '申购',
            type: 'line',
            data: seriesDataBuy,
            smooth: true,
            lineStyle: {
              color: '#EE6666', // 红色
            },
            itemStyle: {
                color: '#EE6666', // 红色
            },
          },
          {
            name: '赎回',
            type: 'line',
            data: seriesDataSell,
            smooth: true,
            lineStyle: {
              color: '#91CC75', // 绿色
            },
            itemStyle: {
                color: '#91CC75', // 绿色
            },
          },
        ],
      };

      this.lineChart.setOption(option);
    },
    // 清空图表
    clearChart() {
      if (this.lineChart) {
        this.lineChart.clear();
      }
    },
  },
}