import axios from 'axios'; // 引入 axios
import * as echarts from 'echarts'; // 引入 ECharts
import { Message } from 'element-ui';

export default {
    name: 'Content_menu2_123',
    props: {
        activeMenu: {
            type: String,
            required: true, 
        },
    },
    data() {
        return {
            tableData: [], // 原始表格数据
            ai_word: '',//ai输出内容
            filteredTableData: [], // 筛选后的表格数据
            filterForm: {
                date: '2024-10-18',//this.getDefaultDate(), // 日期筛选值，设置初始值为当前日期
                品种: '', // 品种筛选值
            },
            total: 0, // 总数据条数
            pageSize: 10, // 每页显示条数
            currentPage: 1, // 当前页码
            sortProp: '', // 当前排序字段
            sortOrder: '', // 当前排序顺序
            isDataLoaded: false, // 标记数据是否已加载
          };
        },
    created() {
        // 页面加载时，直接获取初始日期的数据
        this.fetchData();
    },
    watch: {
        // 监听 activeMenu 的变化
        activeMenu(newVal) {
        if (['2-1', '2-2', '2-3'].includes(newVal)) {
            this.fetchData(); // 当 activeMenu 变化时加载数据
        }
        },
        // 监听表格数据变化，更新饼状图
        filteredTableData() {
        this.$nextTick(() => {
            this.renderPieChart();
        });
        },
    },
    computed: {
        // ai分析框内的文本换行符置换
        formattedAiWord() {
        // 将 \n 替换为 <br>
        return this.ai_word.replace(/\n/g, '<br>');
        },
        // 分页后的数据
        paginatedTableData() {
        const start = (this.currentPage - 1) * this.pageSize;
        const end = start + this.pageSize;
        return this.filteredTableData.slice(start, end);
        },
    },
    methods: {
        // 获取默认日期（当前日期）
        getDefaultDate() {
        const today = new Date();
        const year = today.getFullYear();
        const month = String(today.getMonth() + 1).padStart(2, '0');
        const day = String(today.getDate()).padStart(2, '0');
        return `${year}-${month}-${day}`;
        },
        // 处理菜单选择
        handleMenuSelect(index) {
        this.activeMenu = index;
        },
        // 获取表格数据
        async fetchData() {
        try {
            // 通过 axios 请求后端接口
            const response = await axios.get(
            `http://localhost:3000/menu/${this.activeMenu}/date/${this.filterForm.date}/csv`
            );
            console.log('获取到的数据1:', response.data);
            const response_aiword = await axios.get(
            `http://localhost:3000/menu/${this.activeMenu}/date/${this.filterForm.date}/txt`
            );
            console.log('获取到的数据2:', response_aiword.data);
            this.ai_word = response_aiword.data
            this.tableData = response.data;
            this.filteredTableData = response.data;
            this.total = response.data.length;
            this.isDataLoaded = true; // 标记数据已加载
        } catch (error) {
            console.log('该日无数据');
            // 清空表格数据
            this.ai_word = '无'
            this.tableData = [];
            this.filteredTableData = [];
            this.total = 0;
            this.isDataLoaded = true; // 标记数据已加载（即使无数据）
            // 显示提醒框
            Message.warning('该日期暂无数据');
        }
        },
        // 处理筛选
        handleFilter() {
        this.filteredTableData = this.tableData.filter((item) => {
            // 检查品种是否匹配
            const matchVariety = this.filterForm.品种
            ? item.品种.includes(this.filterForm.品种)
            : true;

            return matchVariety;
        });

        this.currentPage = 1; // 重置到第一页
        },
        // 处理日期变换 
        handleSellectedDate() {
            if (['2-1', '2-2', '2-3'].includes(this.activeMenu)) {
                this.fetchData(); // 当日期变化时重新加载数据
            }
        },
        // 处理分页变化
        handlePageChange(page) {
        this.currentPage = page;
        },
        // 处理排序变化
        handleSortChange({ prop, order }) {
        this.sortProp = prop;
        this.sortOrder = order;
        this.filteredTableData.sort((a, b) => {
            if (order === 'ascending') {
            return a[prop] > b[prop] ? 1 : -1;
            } else {
            return a[prop] < b[prop] ? 1 : -1;
            }
        });
        },
        // 渲染饼状图
        renderPieChart() {
        const chartDom = this.$refs.pieChart;
        if (!chartDom) return; // 确保 DOM 元素存在

        let myChart = echarts.getInstanceByDom(chartDom); // 获取已存在的实例
        if (!myChart) {
            myChart = echarts.init(chartDom); // 如果不存在，则初始化
        } else {
            myChart.clear(); // 如果存在，则清空实例
        }

        // 数据处理：取前19个品种，剩余品种合并为“其他”
        const data = this.filteredTableData
            .map((item) => ({
            name: item.品种,
            value: item['我司成交量(万股)'],
            }))
            .sort((a, b) => b.value - a.value); // 按我司成交量(万股)降序排序

        const top19 = data.slice(0, 19);
        const others = data.slice(19);
        const otherSum = parseFloat(others.reduce((sum, item) => sum + item.value, 0).toFixed(2));

        const pieData = [
            ...top19,
            { name: '其他', value: otherSum },
        ];

        // 配置项
        const option = {
            tooltip: {
            trigger: 'item',
            formatter: '{b}: {c}万股 ({d}%)',
            },
            series: [
            {
                name: '我司成交量(万股)',
                type: 'pie',
                radius: '50%',
                data: pieData,
                emphasis: {
                itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)',
                },
                },
            },
            ],
        };

        myChart.setOption(option);
        }
    },
}