
import { Message } from 'element-ui';
import axios from 'axios';

export default {
  name: 'Content_menu0',
  props: {
    activeMenu: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      filterForm: {
        date: '2024-10-18', // 初始日期
      },
      allIncome:"--",//总利润
      createIncome:"--",//申购利润
      redeemIncome:"--",//赎回利润
      income_ave:"--",//盈利均值
      eff_ave:"--",//交易效率均值
      keep_ave:"--",//连续成交篮子数均值
      ver_num_ourcom:"--",//我司参与品种
      amo_ourcom:"--",//我司参与股数
      marketShare_ave:"--",//市占率均值
      isDataLoaded: false, // 数据加载状态
    };
  },
  created() {
    this.fetchData();
  },

  methods: {
    // 获取数据
    async fetchData() {
      try {
          // 通过 axios 请求后端接口
          const response = await axios.get(
          `http://localhost:3000/menu/${this.activeMenu}/date/${this.filterForm.date}/csv`
          );
          console.log('获取到的数据1:', response.data);
          
          this.allIncome=response.data[0]['总利润'],//总利润
          this.createIncome=response.data[0]['申购利润'],//申购利润
          this.redeemIncome=response.data[0]['赎回利润'],//赎回利润
          this.income_ave=response.data[0]['盈利均值'],//盈利均值
          this.eff_ave=response.data[0]['交易效率均值'],//交易效率
          this.keep_ave=response.data[0]['连续成交篮子数均值'],//连续成交篮子数均值
          this.ver_num_ourcom=response.data[0]['我司参与品种'],//我司参与品种
          this.amo_ourcom=response.data[0]['我司参与股数'],//我司参与股数
          this.marketShare_ave=response.data[0]['市占率均值'],//市占率均值
          this.isDataLoaded = true; // 标记数据已加载（即使无数据）
      } catch (error) {
          console.log('该日无数据');
          // 清空表格数据
          this.allIncome="--",//总利润
          this.createIncome="--",//申购利润
          this.redeemIncome="--",//赎回利润
          this.income_ave="--",//盈利均值
          this.eff_ave="--",//交易效率
          this.keep_ave="--",//连续成交篮子数均值
          this.ver_num_ourcom="--",//我司参与品种
          this.amo_ourcom="--",//我司参与股数
          this.marketShare_ave="--",//市占率均值
          this.isDataLoaded = true; // 标记数据已加载（即使无数据）
          // 显示提醒框
          Message.warning('该日期暂无数据');
      }
    },
  },
}