import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/menu/:menu/date/:date/:type', // 支持动态参数 type
      name: 'fetchData',
      component: {
        template: '<div></div>', // 空组件
      },
    },
  ],
});